* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}

body {
  background: #0f172a;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, sans-serif;
  overflow-x: hidden;
}

html {
  scroll-behavior: smooth;
}

.animated-bg.enhanced {
  z-index: -1;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.gradient-overlay.animated {
  z-index: 0;
  background: linear-gradient(45deg, #667eea66 0%, #764ba266 25%, #f093fb66 50%, #667eea66 75%, #764ba266 100%) 0 0 / 400% 400%;
  width: 100%;
  height: 100%;
  animation: 15s infinite gradientFlow;
  position: absolute;
}

@keyframes gradientFlow {
  0%, 100% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }
}

.blob {
  filter: blur(40px);
  opacity: .3;
  z-index: 2;
  border-radius: 40% 60% 70% 30% / 40% 50% 60%;
  position: absolute;
}

.blob-1 {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  width: 400px;
  height: 400px;
  animation: 20s ease-in-out infinite morphBlob1;
  top: 10%;
  left: 5%;
}

.blob-2 {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  width: 350px;
  height: 350px;
  animation: 25s ease-in-out infinite morphBlob2;
  bottom: 10%;
  right: 10%;
}

.blob-3 {
  background: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%);
  width: 300px;
  height: 300px;
  animation: 30s ease-in-out infinite morphBlob3;
  top: 50%;
  left: 50%;
}

@keyframes morphBlob1 {
  0%, 100% {
    border-radius: 40% 60% 70% 30% / 40% 50% 60%;
    transform: translate(0)rotate(0);
  }

  50% {
    border-radius: 70% 30% 50% 50% / 60% 40%;
    transform: translate(50px, -30px)rotate(180deg);
  }
}

@keyframes morphBlob2 {
  0%, 100% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    transform: translate(0);
  }

  50% {
    border-radius: 30% 70% 70% 30% / 40% 60%;
    transform: translate(-40px, 40px)rotate(-120deg);
  }
}

@keyframes morphBlob3 {
  0%, 100% {
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }

  50% {
    border-radius: 70% 30% 30% 70%;
    transform: translate(calc(40px - 50%), calc(-50% - 40px))rotate(180deg);
  }
}

.ring {
  z-index: 3;
  border: 2px solid #ffffff1a;
  border-radius: 50%;
  position: absolute;
}

.ring-1 {
  width: 500px;
  height: 500px;
  animation: 30s linear infinite rotateRing;
  top: 30%;
  left: 20%;
}

.ring-2 {
  width: 400px;
  height: 400px;
  animation: 40s linear infinite reverse rotateRing;
  bottom: 20%;
  right: 25%;
}

@keyframes rotateRing {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.particles-container {
  z-index: 5;
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
}

.particle {
  pointer-events: none;
  background: #ffffff26;
  border-radius: 50%;
  animation: linear infinite particleFloat;
  position: absolute;
}

@keyframes particleFloat {
  0% {
    opacity: 0;
    transform: translateY(100vh)rotate(0);
  }

  10%, 90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: translateY(-100px)rotate(360deg);
  }
}

.login-container {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  display: flex;
}

.login-card.enhanced {
  backdrop-filter: blur(25px);
  background: #fffffffa;
  border-radius: 24px;
  width: 100%;
  max-width: 480px;
  animation: .8s cubic-bezier(.34, 1.56, .64, 1) cardFadeIn;
  overflow: hidden;
  box-shadow: 0 25px 60px #0000004d, inset 0 0 0 1px #ffffff4d;
}

@keyframes cardFadeIn {
  0% {
    opacity: 0;
    transform: translateY(30px)scale(.95);
  }

  100% {
    opacity: 1;
    transform: translateY(0)scale(1);
  }
}

.login-logo-section {
  text-align: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 35px 40px 25px;
  position: relative;
  overflow: hidden;
}

.logo-circle {
  backdrop-filter: blur(10px);
  background: #ffffff40;
  border: 3px solid #fff6;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 90px;
  margin: 0 auto 20px;
  animation: 4s ease-in-out infinite logoFloat;
  display: flex;
  box-shadow: 0 10px 30px #0003, inset 0 0 20px #ffffff1a;
}

@keyframes logoFloat {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-12px);
  }
}

.logo-icon {
  filter: drop-shadow(0 4px 12px #0000004d);
  font-size: 48px;
}

.login-main-title {
  color: #fff;
  text-shadow: 0 2px 15px #0003;
  letter-spacing: -.5px;
  margin: 0 0 8px;
  font-size: 30px;
  font-weight: 800;
}

.login-tagline {
