  letter-spacing: .3px;
  margin: 0;
  font-size: 15px;
  font-weight: 500;
}

.login-form-enhanced {
  padding: 30px 45px 35px;
}

.form-header {
  text-align: center;
  margin-bottom: 28px;
}

.form-title {
  color: #1e293b;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  margin: 0 0 8px;
  font-size: 26px;
  font-weight: 700;
}

.form-subtitle {
  color: #64748b;
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}

.input-group.enhanced {
  margin-bottom: 24px;
  position: relative;
}

.input-icon-wrapper {
  z-index: 2;
  pointer-events: none;
  font-size: 20px;
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 18px;
  transform: translateY(-50%);
}

.input-group.enhanced:focus-within .input-icon-wrapper {
  transform: translateY(-50%)scale(1.1);
}

.input-field {
  width: 100%;
  font-weight: 500;
  color: #1e293b !important;
  background: #f8fafc !important;
  border: 2px solid #e2e8f0 !important;
  border-radius: 14px !important;
  padding: 16px 52px !important;
  font-size: 15px !important;
  transition: all .3s !important;
}

.input-field:focus {
  transform: translateY(-1px);
  background: #fff !important;
  border-color: #667eea !important;
  outline: none !important;
  box-shadow: 0 0 0 4px #667eea1f, 0 4px 15px #667eea26 !important;
}

.input-field::placeholder {
  color: #94a3b8;
}

.floating-label {
  color: #94a3b8;
  pointer-events: none;
  background: none;
  padding: 0 8px;
  font-size: 15px;
  font-weight: 500;
  transition: all .3s cubic-bezier(.34, 1.56, .64, 1);
  position: absolute;
  top: 50%;
  left: 52px;
  transform: translateY(-50%);
}

.input-field:focus + .floating-label, .floating-label.active {
  color: #667eea;
  background: #fff;
  border-radius: 6px;
  padding: 2px 8px;
  font-size: 12px;
  font-weight: 600;
  top: -2px;
  left: 48px;
}

.password-toggle {
  cursor: pointer;
  z-index: 2;
  background: none;
  border: none;
  border-radius: 8px;
  padding: 6px;
  font-size: 20px;
  transition: all .2s;
  position: absolute;
  top: 50%;
  right: 18px;
  transform: translateY(-50%);
}

.password-toggle:hover {
  background: #f1f5f9;
  transform: translateY(-50%)scale(1.15);
}

.form-options {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 13px;
  display: flex;
}

.remember-checkbox {
  cursor: pointer;
  user-select: none;
  align-items: center;
  gap: 8px;
  display: flex;
}

.remember-checkbox input[type="checkbox"] {
  opacity: 0;
  position: absolute;
}

.checkbox-custom {
  background: #fff;
  border: 2px solid #cbd5e1;
  border-radius: 7px;
  width: 20px;
  height: 20px;
  transition: all .3s;
  position: relative;
}

.remember-checkbox input[type="checkbox"]:checked + .checkbox-custom {
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-color: #667eea;
}

.remember-checkbox input[type="checkbox"]:checked + .checkbox-custom:after {
  content: "✓";
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.checkbox-label {
  color: #475569;
  font-weight: 500;
}

.forgot-link {
  color: #667eea;
  font-weight: 600;
  text-decoration: none;
  transition: all .2s;
  position: relative;
}

.forgot-link:after {
  content: "";
  background: #667eea;
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: -2px;
  left: 0;
}

.forgot-link:hover:after {
  width: 100%;
}

.btn-login.enhanced {
  color: #fff;
  cursor: pointer;
  letter-spacing: .5px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 14px;
  justify-content: center;
  align-items: center;
  gap: 12px;
