  box-shadow: 0 6px 20px #667eea66;
}

.btn-login.enhanced:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff6, #0000);
  width: 100%;
  height: 100%;
  transition: left .6s;
  position: absolute;
  top: 0;
  left: -100%;
}

.btn-login.enhanced:hover:before {
  left: 100%;
}

.btn-login.enhanced:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 35px #667eea80;
}

.btn-login.enhanced:disabled {
